export default {
	data() {
		return {
			currentNum:1,
			stu:null,
			dataList:null,
			pageType:null
		}
	},
	methods: {
		next()
		{
			this.currentNum++;
			this.loadStudentInfo()
			
			
		},
		prevent()
		{
			this.currentNum--;
			this.loadStudentInfo()
		},
		
		
		loadStudentInfo()
		{
			uni.request({
				url: this.serverUrl + '/stu/mystudent/'  + this.pageType + '/' + this.currentNum + '?name=&limit=1',
				method: 'GET',
				data: {},
				success: res => {
					this.stu = res.data[0]
				},
				fail: () => {},
				complete: () => {}
			});
		},
		stuModify(id)
		{
			uni.navigateTo({
				url:"../stu_modify/stu_modify?id=" + id
			})
		},
		// 标记优生
		setGood()
		{
			uni.request({
				url: this.serverUrl + '/stu/sign_cancel_good/' + this.stu.id,
				method: 'GET',
				data: {},
				success: res => {
					console.log(res.data)
					if(res.data.status==200)
					{
						uni.showToast({
							title: res.data.msg
						})
						return;
					}
					
					uni.showToast({
						title: "未知错误！"
					})
					
					
				},
				fail: () => {},
				complete: () => {}
			});
			
		}
		
	},
	//跳转到回访页面
	returnVisit()
	{
	 	uni.navigateTo({
	 		url:"../stu_return_visit/stu_return_visit"
	 	})	
	},
	computed:{
		hasPrevent:function(){
			return{
				'bg-red': true
			}
		}
	},
	onShow(param) {
		var me = this;
		this.loadStudentInfo()
		
		
		
	},
	onLoad(param)
	{
		console.log("load... type:" + param.type)
		this.pageType = param.type
	}
	
}
